const superagent = require("superagent");

async function fetchPublicCountyData(state_short, county_fips) {
  return superagent
    .get(`${process.env.PUBLIC_URL}/AllData/${state_short}/${county_fips}.json`)
    .then(res => {
      return res.body;
    });
}

async function fetchAllUSData() {
  return superagent
    .get(`${process.env.PUBLIC_URL}/AllData/AllData.json`)
    .then(res => {
      return res.body;
    });
}

async function fetchWorldData() {
  return superagent
    .get(`${process.env.PUBLIC_URL}/AllData/WorldData.json`)
    .then(res => {
      return res.body;
    });
}

export {
  fetchPublicCountyData,
  fetchAllUSData,
  fetchWorldData
}